<template>
  <section>
    <h3>Create character</h3>
    <div>
      <input type="file" accept="image/*" @change="onChange" />
      <img v-if="imageUrl" :src="imageUrl" />

      <input type="text" placeholder="character's name" v-model="char.nickname" />
      <textarea v-model="char.profile" cols="30" rows="10"></textarea>
      <button @click="Save()">Save</button>

      <router-link v-bind:to="{name:'create_skills'}">Create skill</router-link>
      <button @click="upload">Upload character</button>
    </div>
  </section>
</template>


<script>
import Axios from "axios";

export default {
  data() {
    return {
      char: {
        nickname: null,
        profile: null,
        facepic: {}
      },
      imageUrl: null,
      saved: false
    };
  },

  methods: {
    onChange(ev) {
      this.char.facepic = ev.target.files[0];
      this.imageUrl = URL.createObjectURL(this.char.facepic);
    },

    Save() {
      this.$store.commit("character/saveProfile", this.char);
      this.saved = true;
    },

    upload() {
      const form = this.$store.getters["character/request"];
      Axios.post("/admin/new/persona", form, {
        headers: {
          "Content-Type": "application/json"
        }
      });
    }
  }
};
</script>